@import url(https://fonts.googleapis.com/css2?family=Lato:ital@1&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@200;300;400;500;600;700&display=swap);
/*font*/
/*Navbar*/

:root {
  --cvWhite: #fff;
  --cvGreen:rgb(56, 186, 89);
  --cvPink: rgb(255, 77, 110);
  --cvBlue: rgb(0, 25, 90);
  --cvOrange: rgb(255, 115, 77);
  --cvYellow: rgb(255, 197, 26);
  --cvDark: #212529;
  --cvRed: #d9534f;
  --cvLightGrey: 	rgb(211,211,211);
  --cvDarkGrey: rgb(105,105,105);
}


body{
  font-family: "Lato";
}

/* logo{
  width: 16.25rem;
  max-height: 10rem;
} */

/*NavLinks*/

nav a.nav-link {

  font-size: .8rem;
  initial-letter: 0.0375rem;
  cursor: pointer
}

nav ul li{
  text-transform: uppercase;
}

.navbar-nav > li > a {
  margin-right: 4.5rem;
  margin-left: 4.5rem;
  text-align: center;
  color: rgb(255, 115, 77) !important;
  color: var(--cvOrange) !important;
  border-top:  0.1875rem solid #212529;
  border-top:  0.1875rem solid var(--cvDark); 
  font-family: Oswald;
  letter-spacing: 0.5rem;
  font-weight: 300;
}

.navbar-brand{
  max-width: 17.25rem;
  padding: 0 !important;
  height: 6rem !important;
} 

.navbar-light .navbar-brand {
  color: #fff !important;
  color: var(--cvWhite) !important;
  text-transform: uppercase;
  font-size: 16px;
  height: 5rem !important;
}

.navbar-nav > li > a.active, .navbar-nav > li > a.active:focus{
  border-top: 0.1875rem solid #fff;
  border-top: 0.1875rem solid var(--cvWhite);
}

.navbar-nav > li > a:hover{
  border-top: 0.1875rem solid #fff;
  border-top: 0.1875rem solid var(--cvWhite);
}

/***Header***/
.header-wrapper{
  background-position: relative;
  background: url(/static/media/background.0397e54d.jpg) no-repeat center center;
  background-size: cover;
  opacity: 0.9;
  -webkit-filter: brightness(80%);
          filter: brightness(80%);
  
  background-position: cover;
  height: 100vh;
}

.main-info{
  display: -webkit-flex;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  right:0;
  bottom: 0;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
  font-family: "Oswald", sans-serif;
  font-weight: 500;
} 

.main-info h1{
  color: rgb(255, 115, 77);
  color: var(--cvOrange);
  text-transform: uppercase;
  font-family: 'Oswald';
  font-weight: 200;
}


.typed-text{
  font-size: 2rem;
  color: #fff;
  color: var(--cvWhite)
}

.btn-main-offer{
  border: 1px solid #d9534f;
  border: 1px solid var(--cvRed);
  text-transform: uppercase;
  border-radius: 0;
  padding: 0.625rem 0.75rem 0.75rem 0.75rem;
  color: #fff;
  color: var(--cvWhite);
  margin: 2rem;
}

/*About Me */

.photo-wrap{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.about-me.container{
  background-color: aqua;
}

.profile-img{
  width: 16rem;
  border-radius: 50%;
  padding: .3rem;
  border: 1px solid rgb(255, 115, 77);
  border: 1px solid var(--cvOrange);
}

.about-heading{
  color: rgb(255, 115, 77);
  color: var(--cvOrange);
  text-transform: uppercase;
  font-family: Oswald;
  font-weight: 300;
  letter-spacing: 0.5rem;
}

@media(max-width: 468px){
  .profile-img{
    width: 10rem;
  }
}

/****Services***/

.services{
  text-align: center;
  font-family: Oswald;
  font-weight: 200;
  padding-bottom: 2rem;
  background-color: rgb(211,211,211);
  background-color: var(--cvLightGrey);
}

.services h1{
  color: black;
  text-transform: uppercase;
  letter-spacing: 0.75rem;
  font-weight: 200;
}

.services ul{
  position: center;
}

.services .circle{
  position: relative;
  margin: 0.375rem auto;
  background: rgb(255, 115, 77);
  background: var(--cvOrange);
  border-radius: 50%;
  width: 3.125rem;
  height: 3.125rem;
}

.services .icon{
  font-size: 1.4rem;
  color: #fff;
  color: var(--cvWhite);
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
}

.services .box{
  height: 16rem;
  border-bottom: 0.3125rem solid rgb(255, 115, 77);
  border-bottom: 0.3125rem solid var(--cvOrange);
  padding: 0.625rem;
  margin-bottom:  1.875rem;
  background: #fff;
  background: var(--cvWhite);
}

.services .box:hover{
  background: rgb(255, 115, 77);
  background: var(--cvOrange);
  border-bottom: 0.3125rem solid #212529;
  border-bottom: 0.3125rem solid var(--cvDark);
}

.services .box:hover .icon {
  color: #212529;
  color: var(--cvDark);
}
.services .box:hover .circle {
  background: #fff;
  background: var(--cvWhite);
}

.services .box:hover p {
  color: #fff;
  color: var(--cvWhite);
}

/************Experience**************/

.experience-wrapper{
  position: relative;
  padding: 3.125rem;
  overflow: hidden;
}

.experience h1{
  margin-top: 3rem;
  text-transform: uppercase;
  color: rgb(255, 115, 77);
  color: var(--cvOrange);
  font-family: Oswald;
  font-weight: 200;
  letter-spacing: 0.75rem;
}

.experience h3{
  letter-spacing: 0.25rem;
}

.experience-wrapper::before{
  position: absolute;
  content: "";
  top: 0;
  left: 50%;
  margin-left: -0.0625rem;
  width: 0.125rem;
  height: 100%;
  background: rgb(211,211,211);
  background: var(--cvLightGrey);
  z-index: 1;
}

.timeline-block{
  width: calc(50% + 0.5rem);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  clear: both;
}

.timeline-block-left{
  float: left;
  direction: rtl;
}

.timeline-block-right{
  float: right;
  
}

.marker{
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  border: 0.3125rem solid rgb(105,105,105);
  border: 0.3125rem solid var(--cvDarkGrey);
  background: rgb(255, 115, 77);
  background: var(--cvOrange);
  margin-top: 0.625rem;
  z-index: 3;
}

.timeline-content {
  width: 95%;
  padding: 0 0.9375rem;
  color: rgb(105,105,105);
  color: var(--cvDarkGrey);
  direction: ltr;
}

.timeline-content h3 {
  margin-top: 0.3125rem;
  color: #d9534f;
  color: var(--cvRed);
  font-weight: 900;
}

/********MOBILE*************/

@media(max-width: 768px) {
.experience .container {
  padding-left: 0;
}

  .experience-wrapper::before {
    left: 0.5rem;
    margin-bottom: 1.875rem;
  }
  .timeline-block-right {
    float: none;
    width: 100%;
  }
  
  .timeline-block-left {
    float: none;
    direction: ltr; 
    width: 100%;
  }
}


/*********Contact*************/

.contacts{
  background-color: #212529;
  background-color: var(--cvDark);
  padding: 3rem 0;
}

.contacts h1{
  color: rgb(255, 115, 77);
  color: var(--cvOrange);
  padding: 1.3rem;
  text-transform: uppercase;
  font-family: 'Oswald';
  font-weight: 200;
  letter-spacing: 0.75rem;
}

.contacts p{
  color: #fff;
  color: var(--cvWhite);
  padding: 1.3rem;
}
/*****Inputs****/

.contacts input, .contacts textarea{
  font-family: Lato;
  width: 100%;
  box-sizing: border-box;
  background: none;
  resize: none;
  border: 0;
  border-bottom: 0.125rem solid rgb(105,105,105);
  border-bottom: 0.125rem solid var(--cvDarkGrey);
  border-radius: 0;
  color: rgb(211,211,211);
  color: var(--cvLightGrey);
  outline: 0 !important;
  box-shadow: none !important;
  top: 0;
  margin-bottom: 1rem;
}


.contacts .container{
  padding: 4rem 0;
}


.contacts input:focus,  .contacts textarea:focus{
  background: transparent;
  color: rgb(255, 115, 77);
  color: var(--cvOrange);
  border-bottom: 0.125rem solid rgb(105,105,105);
  border-bottom: 0.125rem solid var(--cvDarkGrey);

}

.contacts textarea{
  height: 12.4rem !important;
  margin: 0;
}

.contact-btn {
  margin: 1rem 0 0 0;
  width: 100%;
  background: none;
}

.line{
  position: relative;
  top: -0.8rem;
  width: 0;
  height: 0.125rem;
  background: rgb(255, 115, 77);
  background: var(--cvOrange);
  display: inline-block;
}

input:focus+.line, textarea:focus+.line{
  width: 100%;
  transition: width 0.5s ease-in-out;
}

input.form-control{
  margin: -0.3rem 0;
}

textarea.form-control{
  margin: -0.3rem 0;
}

.error-message{
  color: #fff;
  color: var(--cvWhite);
  background: #d9534f;
  background: var(--cvRed);
  text-align: left;
}

.success-message{
  font-weight: 900;
  color: rgb(56, 186, 89);
  color: var(--cvGreen);
}
/****Media Contact****/

@media(max-width: 768px){
  .contacts{
  overflow-x: hidden;
  }
  .contacts .container{
    padding:0 1rem;
  }
  .contacts p {
    padding: 0 0 4rem;
  }
}




